import React , {useState} from 'react'
import emailjs from 'emailjs-com'
import {FaEnvelope, FaMapMarkerAlt, FaPhoneAlt, FaRegSmileWink} from "react-icons/fa";
import './Contact.css'

function Contact() {
    const [send, setSend] = useState(false);
    const [isLoading, setIsLoading] = useState(false);

    function sendEmail(e) {
        e.preventDefault();
        setIsLoading(true);
        emailjs.sendForm('akezanna93', 'akezanna93', e.target, 'user_YfGshJVafGym9S1F1omjd')
          .then((result) => {
             setIsLoading(false);
              setSend(true);
          }, (error) => {
              console.log(error.text);
          });
          e.target.reset();
      }
    
      return (
          <div className="contact" id="contact">
              <div className="title">
                    <h1>Contact me</h1>
              </div>
              <div className="contact-sec">
                    <div className="personel-info">
                            <div className="name">
                                <span><FaRegSmileWink/></span>
                                <h2>abderrafi kezannaya</h2>
                            </div>
                            <div className="local name">
                                <span><FaMapMarkerAlt/></span>
                                <h3>Aklim Berkane, Morocco</h3>
                            </div>
                            <div className="email name">
                                <span><FaEnvelope /></span>
                                <h4>akezannaya@gmail.com</h4>
                            </div>
                            <div className="phone name">
                                <span><FaPhoneAlt /></span>
                                <h4>+212667880720</h4>
                            </div>
                    </div>
                    <form className="contact-form" onSubmit={sendEmail}>
                        {send && <span className="notif">your message sending successfully.</span>}
                        {
                            isLoading && <h3 style={{color:"blue"}}>Sending...</h3>
                        }
                        <div className="input-field">
                            <label>Full Name</label>
                            <input type="text" name="name" placeholder="full name" required />
                        </div>
                        <div className="input-field">
                            <label>Email</label>
                            <input type="email" name="email" placeholder="email" required/>
                        </div>
                        <div className="input-field">
                            <label>Message</label>
                            <textarea name="message" placeholder="message" required/>
                        </div>
                        <div className="input-field btn">
                            <button type="submit">Send</button>
                        </div>
                    
                    </form>
              </div>

        </div>
      );
    }

export default Contact;
